@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@400;500&display=swap);
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: #eeeeee;
  box-sizing: border-box;
  font-family: 'Prompt', sans-serif;
}

* {
  box-sizing: border-box;
}

::selection {
  color: #fff;
  background: #3a4750;
}

.header {
  width: 100%;
  height: 8%;
  background-color: #3a4750;
  display: flex;
  padding: 1rem 1rem 1rem 3.5rem;
  align-items: center;
  justify-content: flex-start;
}

.title span {
  color: #fff;
  font-size: 2rem;
  letter-spacing: 0.1em;
  transition: all 0.3s ease-out;
}

.title span:nth-child(2) {
  opacity: 0;
  overflow: hidden;
  transition: ease-in-out 0.5s;
  letter-spacing: -0.5em;
}

.title span:nth-child(5) {
  opacity: 0;
  overflow: hidden;
  transition: ease-in-out 0.5s;
  letter-spacing: -0.5em;
}

.title span:nth-child(2) {
  transition-delay: 0.35s;
}

.title:hover span:nth-child(2) {
  opacity: 1;
  letter-spacing: 0.1em;
  transition-delay: 0s;
}

.title:hover span:nth-child(5) {
  opacity: 1;
  letter-spacing: 0.1em;
  transition-delay: 0.35s;
}

span.heart {
  color: #d9534f;
}

span {
  cursor: default;
}

.list {
    border-radius: 1rem;
    margin: 1.5rem;
    background-color: #bcd7e7;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    padding: 1.5rem;
}

.todo {
  width: 100%;
  padding: 0 .2rem;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo-actions {
    width: -webkit-min-content;
    width: min-content;
    -webkit-transform: translateX(.7rem);
            transform: translateX(.7rem);
}

.todo-btn {
    cursor: pointer;
}

.check-btn {
    color: #5cb85c;
}

.delete-btn {
    color: #555;
    margin-right: 0;
}

.todo-content {
    margin-right: 10vw;
    cursor: text;
    color: #547494;
}

.complete {
    color: #9CB4CC;
}

[contenteditable] {
    outline: 0px solid transparent;
}

.todo-form {
    margin: 0 0 1rem 0;
}

